import React from 'react';
import kuervoFooter from '../assets/images/kuervo-footer.png';
import { Container, Row, Col } from 'react-bootstrap';
import { FaFacebook } from 'react-icons/fa';

function Footer() {

    return(
        <footer>
            <Container>
                <Row className="border-top justify-content-between p-3">

                    <Col className="p-0 d-flex flex-column justify-content-center" lg={8} md={6}>
                        <p class="mb-2 display-3 font-weight-bolder">Follow Us </p>
                        <p class="mt-4 display-4 font-weight-light"><a href="https://www.facebook.com/kuervonline" target="_blank" rel="noreferrer" class="mr-2">
                            <FaFacebook className="facebook-icon" /></a></p>
                    </Col>

                    <Col className="p-0 d-flex justify-content-center text-center" lg={4} md={6}>
                        <img className="footer-content-image" alt="timer" src={kuervoFooter} />
                    </Col>
                    
                </Row>
            </Container>
        </footer>
    );
}

export default Footer;