import React from 'react';
//Librerias//
import Zoom from 'react-reveal/Zoom';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { AnimateKeyframes } from "react-simple-animate";
//Imagenes//
import darkMagicianGirl from '../assets/images/tcgHome01.png';
import yugioh from '../assets/images/tcgyugioh.jpg';
import magic from '../assets/images/tcgmagic.jpg';
import pokemon from '../assets/images/tcgpokemon.jpg';
import vanguard from '../assets/images/tcgvanguard.jpg';
import weiss from '../assets/images/tcgweiss.jpg';
import forceofwill from '../assets/images/tcgforce.jpg';


function ContentHomeTCG(props) {

    return(
        <Container fluid={true}>
            <Row className="justify-content-center">
                <Col md={4} className="home-content">
                    {props.children}
                </Col>
                <Col md={8}>
                    <Zoom>
                        <AnimateKeyframes 
                            play={true}
                            iterationCount="infinite"
                            duration={5}
                            keyframes={[
                                "transform: translateX(-250px)",
                                "transform: translate(250px, -150px)",
                                "transform: translate(150px, 250px)",
                                "transform: translate(-250px)"
                            ]}>
                            <img className="d-none d-md-none d-lg-block home-content-image-tcg" alt="timer" src={darkMagicianGirl} />
                        </AnimateKeyframes>
                        </Zoom>
                </Col>
            </Row>
            <Row className="justify-content-center mt-4">
                <Col md={4} sm={6}>
                    <Zoom>
                        <Card.Img className="about-skill mb-4" variant="top" alt="yugioh" src={yugioh} />
                    </Zoom>
                </Col>
                <Col md={4} sm={6}>
                    <Zoom>
                        <Card.Img className="about-skill mb-4" variant="top" alt="magic" src={magic} />
                    </Zoom>
                </Col>
                <Col md={4} sm={6}>
                    <Zoom>
                        <Card.Img className="about-skill mb-4" variant="top" alt="pokemon" src={pokemon} />
                    </Zoom>
                </Col>
                <Col md={4} sm={6}>
                    <Zoom>
                        <Card.Img className="about-skill mb-4" variant="top" alt="vanguard" src={vanguard} />
                    </Zoom>
                </Col>
                <Col md={4} sm={6}>
                    <Zoom>
                        <Card.Img className="about-skill mb-4" variant="top" alt="weiss" src={weiss} />
                    </Zoom>
                </Col>
                <Col md={4} sm={6}>
                    <Zoom>
                        <Card.Img className="about-skill mb-4" variant="top" alt="forceofwill" src={forceofwill} />
                    </Zoom>
                </Col>
            </Row>
        </Container>
    );

}

export default ContentHomeTCG;