import React from 'react';
import homecarousel1 from '../assets/images/home-carousel-1.jpg';
import homecarousel2 from '../assets/images/home-carousel-2.jpg';
import homecarousel3 from '../assets/images/home-carousel-3.jpg';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';

class CarouselHome extends React.Component {
    render() {
        return (
            <Carousel infiniteLoop autoPlay useKeyboardArrows>
                <div>
                    <img alt="PRE-SALE" src={homecarousel1} />
                    <p className="legend">KANNA FROM MISS KOBAYASHI 1/6</p>
                </div>
                <div>
                    <img alt="PRE-SALE" src={homecarousel2} />
                    <p className="legend">MIZUHO KAZAMI FROM ONEGAI TEACHER</p>
                </div>
                <div>
                    <img alt="PRE-SALE" src={homecarousel3} />
                    <p className="legend">VELVET CROWE FROM TALES OF BERSERIA 1/8</p>
                </div>
            </Carousel>
        );
    }
};


export default CarouselHome;